.divider {
  font-weight: 300;
}
.divider[ui-mode~="0%"]:after,
.divider[ui-mode~="0%"]:before {
  width: 0;
}
.list > .item > .loadmore {
  margin-top: 0;
  margin-bottom: 0;
}
.list > .item .input-widget .input {
  margin-right: 30px;
}
.list > .item .input-widget .input + .icon {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  line-height: 50px;
  padding: 0 15px;
  margin: 0;
}
.list.overlap > .item:first-child:before {
  content: none;
}
.navbar.overlap:before {
  content: none;
}
header.bar {
  background-color: #f7f7f7;
}
header.bar > .menu {
  line-height: 50px;
}
footer.bar {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 50;
  min-height: 50px;
  height: auto;
}
.swing.fixed .tablet {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: #f9f9f9;
  transform: translate3d(0, 0, 0);
}
.swing.shadow.fixed .tablet {
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}
.swing.nested.fixed .tablet {
  top: 0;
}
.tabbar {
  position: static;
}
.tabbar > .item .icon img {
  width: 48px;
  height: auto;
}
.ex-widget-layer.ex-widget-enter {
  background-color: rgba(0, 0, 0, 0);
}
.ex-widget-layer.ex-widget-enter.ex-widget-enter-active:not(.transparent) {
  background-color: rgba(0, 0, 0, 0.6);
}
.ex-widget-layer.ex-widget-exit:not(.transparent) {
  background-color: rgba(0, 0, 0, 0.6);
}
.ex-widget-layer.ex-widget-exit:not(.transparent).ex-widget-exit-active {
  background-color: rgba(0, 0, 0, 0);
}
.toast {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120px;
  min-height: 120px;
  margin: -120px 0 0 -60px;
  z-index: 1000;
  display: flex;
  align-items: center;
  transform: scale(1);
}
.toast .icon.waiting {
  width: 38px;
  height: 38px;
}
.toast > * {
  margin: auto;
}
.toast.ex-widget-enter {
  opacity: 0;
  transform: scale(1.185);
}
.toast.ex-widget-enter.ex-widget-enter-active {
  opacity: 1;
  transform: scale(1);
}
.toast.ex-widget-exit {
  opacity: 1;
  transform: scale(1);
}
.toast.ex-widget-exit.ex-widget-exit-active {
  opacity: 0;
  transform: scale(0.85);
}
.ex-widget-enter .toast {
  opacity: 0;
  transform: scale(1.185);
}
.ex-widget-enter.ex-widget-enter-active .toast {
  opacity: 1;
  transform: scale(1);
}
.ex-widget-exit .toast {
  opacity: 1;
  transform: scale(1);
}
.ex-widget-exit.ex-widget-exit-active .toast {
  opacity: 0;
  transform: scale(0.85);
}
.modal > .content {
  padding: 30px 15px 30px;
}
.modal > .title + .content {
  padding: 10px 15px 16px;
}
.ex-widget-exit .modal {
  opacity: 1;
  transform: scale(1);
}
.ex-widget-exit.ex-widget-exit-active .modal {
  opacity: 0;
  transform: scale(0.85);
}
.ex-widget-exit .actionsheet {
  transform: translate3d(0, 0, 0);
}
.ex-widget-exit.ex-widget-exit-active .actionsheet {
  transform: translate3d(0, 100%, 0);
}
.popup {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  transform: translate3d(0, 0, 0);
  transition-property: transform;
  transition-duration: .3s;
}
.popup > .header {
  position: relative;
  height: 44px;
  background-color: #f7f7f7;
  display: block;
  width: 100%;
  display: flex;
  align-items: center;
}
.popup > .header .title {
  flex: 1;
  font-weight: 400;
  text-align: center;
}
.popup > .header:after {
  content: ' ';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.popup > .content {
  height: 280px;
  height: 60vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.popup > .content > .list:first-child {
  margin-top: -1px;
}
.ex-widget-enter .popup,
.ex-widget-enter .picker {
  transform: translate3d(0, 100%, 0);
}
.ex-widget-enter.ex-widget-enter-active .popup,
.ex-widget-enter.ex-widget-enter-active .picker {
  transform: translate3d(0, 0, 0);
}
.ex-widget-exit .popup,
.ex-widget-exit .picker {
  transform: translate3d(0, 0, 0);
}
.ex-widget-exit.ex-widget-exit-active .popup,
.ex-widget-exit.ex-widget-exit-active .picker {
  transform: translate3d(0, 100%, 0);
}
.feedback:not(.compact) .mark {
  padding: 33% 35px 35px;
}
.carousel {
  position: relative;
  overflow: hidden;
  background-color: #f2f2f2;
}
.carousel .roller {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.carousel .roller .slider {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.carousel .roller .slider .frame {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}
.carousel .roller .slider .frame img {
  flex: 1;
  width: 100%;
}
.carousel .decorators {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  font-size: 0;
  text-align: center;
  line-height: normal;
}
.carousel .decorators .item {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  border-radius: 100%;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: rgba(150, 150, 150, 0.1);
}
.carousel .decorators .item.active {
  border: none;
  background-color: #fda413;
}
html,
body {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
body {
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  touch-action: manipulation;
  word-wrap: break-word;
  text-size-adjust: none;
  user-select: none;
  -webkit-user-drag: none;
  -ms-content-zooming: none;
}
#root,
#app,
#main,
#main .view {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
}
#main > .bundle {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  transform: translate3d(0, 0, 0);
}
#main .view {
  background-color: #f2f2f2;
  transform: translate3d(0, 0, 0);
}
#main .view > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  transform: translate3d(0, 0, 0);
}
#main .view > .content.coord {
  transform: none;
}
a {
  -webkit-touch-callout: none;
}
.addrbar {
  background: #fff url(//img1.qdingnet.com/e200d887fa968c85be60a262218dadb8.png?imageslim) center 0 repeat-x;
  background-size: auto 2px;
}
.popup.coupon h3 {
  font-size: 40px;
  min-width: 50px;
  margin-right: 12px;
  text-align: center;
}
@keyframes moveltc {
  from {
    transform: translate3d(100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes movectl {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(-30%, 0, 0);
  }
}
@keyframes movertc {
  from {
    transform: translate3d(-100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes movectr {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(30%, 0, 0);
  }
}
@keyframes movebtm {
  from {
    transform: translate3d(0, 100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes movemtt {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, -100%, 0);
  }
}
@keyframes movettm {
  from {
    transform: translate3d(0, -100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes movemtb {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, 100%, 0);
  }
}
@keyframes scaledw {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.8);
  }
}
.ex-move-ltr-enter {
  z-index: 200;
  animation: moveltc 0.3s ease-out both;
}
.ex-move-ltr-exit {
  z-index: 100;
  animation: scaledw 0.3s ease-out both;
}
.ex-move-rtl-enter {
  z-index: 200;
  animation: movertc 0.3s ease-out both;
}
.ex-move-rtl-exit {
  z-index: 100;
  animation: scaledw 0.3s ease-out both;
}
.ex-move-btt-enter {
  animation: movebtm 0.3s ease-out both;
}
.ex-move-btt-exit {
  animation: movemtt 0.3s ease-out both;
}
.ex-move-ttb-enter {
  animation: movettm 0.3s ease-out both;
}
.ex-move-ttb-exit {
  animation: movemtb 0.3s ease-out both;
}
/* logistics */
.logistics .flow:after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-left: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 0;
  transform: scaleX(0.5);
  width: 0;
  left: 90px;
}
.logistics .flow .item {
  background-color: transparent;
}
.logistics .flow .item:before,
.logistics .flow .item:after {
  content: none;
}
.logistics .flow .item .time {
  position: relative;
  width: 90px;
  padding: 15px 15px 15px 0;
}
.logistics .flow .item .time:after {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  width: 10px;
  height: 10px;
  background: #a9a9a9;
  border-radius: 50%;
  transform: translate(50%, -50%);
  z-index: 10;
}
.logistics .flow .item:first-child {
  color: #1cace8;
}
.logistics .flow .item:first-child .time:after {
  background-color: #1cace8;
}



