:local(.home) {
  background-color: #f2f2f2;
}
:local(.home) aside {
  position: fixed !important;
  left: 0;
  top: 50px;
  bottom: 58px;
  width: 90px;
  font-size: 14px;
  text-align: center;
  transform: translate3d(0, 0, 0);
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  border-right: 1px solid #eee;
}
:local(.home) aside.nested {
  top: 0;
}
:local(.home) aside .item {
  position: relative;
  display: block;
  line-height: 52px;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
:local(.home) aside .item.active {
  color: #fda413;
  background-color: #fff;
}
:local(.home) aside .item.active:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-left: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 0;
  transform: scaleX(0.5);
  border-width: 4px;
  border-color: #fda413;
}
:local(.home) .groups {
  display: block;
  margin-left: 90px;
  background-color: #fff;
}
:local(.home) .groups h4 {
  padding: 0 15px;
  line-height: 52px;
}
:local(.home) .groups .items {
  overflow: hidden;
}
:local(.home) .groups .items:after {
  content: '\0200';
  visibility: hidden;
  clear: both;
  display: block;
  font-size: 0;
  height: 0;
}
:local(.home) .groups .items .item {
  float: left;
  width: 33.33333%;
  text-align: center;
  margin-bottom: 20px;
}
:local(.home) .groups .items .item:active {
  opacity: 0.8;
}
:local(.home) .groups .items .item img {
  border-radius: 50%;
  display: block;
  margin: 0 auto 5px;
}



